.main {
  display: flex;
  position: relative;
  height: 100%;
}
.main-left {
  width: 150px;
  position: absolute;
}
.main-left h5 {
  background: #0099ff;
  height: 40px;
  width: 100px;
  text-align: center;
  line-height: 40px;
  border-radius: 5px;
}
.main-right {
  margin-left: 150px;
  z-index: 1;
  height: 100%;
  overflow-y: auto;
  flex: 1 1;
}
.main-right h4 {
  font-size: 24px;
  font-weight: 700;
}
.main-right .plans-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.main-right .plans-wrapper .plan {
  height: 97%;
}
