.register {
  width: 100%;
  height: 100%;
  padding-top: 10%;
}
.register .login-header {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  font: 2em sans-serif;
}
.register .login-content {
  width: 400px;
  height: 300px;
  background: #fff;
  margin: auto;
  padding: 20px 40px;
}
.register .login-content h2 {
  font: 1.8em sans-serif;
  text-align: center;
  font-weight: 300;
}
.register .login-content .login-form {
  margin-top: 47px;
}
.register .login-content .login-form .login-form-button {
  width: 100%;
}
