.plan-card {
  position: relative;
  width: 800px;
  margin-top: 20px;
  padding: 30px 30px 10px 100px;
  border: 1px solid #888;
}
.plan-card .card-title {
  position: absolute;
  left: 30px;
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: #555555;
}
.plan-card .card-teacher {
  display: flex;
}
.plan-card .card-teacher .teacher-detail,
.plan-card .card-teacher .course-detail,
.plan-card .card-teacher .time-detail {
  flex: 1 1;
  margin-left: 10px;
  border: 1px solid #888;
}
.plan-card .card-teacher .teacher-detail .left,
.plan-card .card-teacher .course-detail .left,
.plan-card .card-teacher .time-detail .left {
  display: inline-block;
  margin-right: 10px;
  width: 30%;
  min-width: 80px;
  border-right: 1px solid #888;
  background-color: #22c4f7;
  text-align: center;
}
.plan-card .card-source {
  margin-top: 10px;
  display: flex;
  border-top: 1px solid #888;
  line-height: 30px;
}
.plan-card .card-source .plan-topic {
  flex: 1 1;
  padding-left: 10px;
}
.plan-card .plan-source {
  height: 80px;
  border-top: 1px solid #888;
}
.plan-card .plan-foot {
  margin-top: 30px;
  display: flex;
}
.plan-card .plan-foot span {
  flex: 1 1;
  color: #169BD5;
}
