.plan {
  border: 1px solid #888;
  width: 80%;
  overflow: auto;
  margin: auto;
  padding: 50px 30px 10px 90px;
  margin-bottom: 20px;
}
.plan-describe {
  position: relative;
  margin-bottom: 10px;
}
.plan-describe .plan-title {
  left: -70px;
  position: absolute;
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: #555555;
}
.plan-describe-inline-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.plan-describe-inline-item-item {
  display: flex;
  align-items: center;
  flex: 1 1;
  margin-right: 10px;
  border: 1px solid #888;
}
.plan-describe-inline-item-item:last-child {
  margin-right: 0;
}
.plan-describe-inline-item-item span {
  display: block;
  width: 30%;
  min-width: 80px;
  border-right: 1px solid #888;
  background-color: #22c4f7;
  text-align: center;
  height: 32px;
  line-height: 31px;
}
.plan-describe-secondline {
  display: flex;
  border-top: 1px solid #888;
  border-bottom: 1px solid #888;
}
.plan-describe-secondline-item {
  flex: 1 1;
  display: flex;
}
.plan-describe-secondline-item span {
  min-width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.plan-content {
  margin-bottom: 10px;
}
.plan-content-title {
  display: inline-block;
  line-height: 110px;
  width: 100%;
  text-align: center;
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  margin: 20px 0;
  background-color: hsla(0, 0%, 94.9%, 0.82745098);
  border: 1px solid #888;
}
.plan-content .plan-content-top-wrapper {
  display: flex;
}
.plan-content .plan-content-top-wrapper .plan-content-left {
  flex: 1 1;
  margin-right: 20px;
}
.plan-content .plan-content-top-wrapper .plan-content-left .content-input {
  border: 1px solid #888;
  padding: 10px;
  margin-bottom: 10px;
}
.plan-content .plan-content-top-wrapper .plan-content-left .content-input .my-plan {
  display: flex;
  align-items: center;
  justify-items: center;
}
.plan-content .plan-content-top-wrapper .plan-content-left .content-input .TextArea {
  flex: 1 1;
  border-color: #afeeee;
}
.plan-content .plan-content-top-wrapper .plan-content-left .content-input .TextArea:hover {
  border-color: #fff !important;
}
.plan-content .plan-content-top-wrapper .plan-content-left .content-input .TextArea:focus {
  border-color: #fff !important;
}
.plan-content .plan-content-top-wrapper .plan-content-left .continue-add {
  margin-bottom: 20px;
}
.plan-content .plan-content-top-wrapper .plan-content-left .continue-add .content-btn {
  margin-top: 20px;
  display: inline-block;
  text-align: center;
  width: 236px;
  line-height: 71px;
  border-radius: 5px;
  border: 1px dashed #888;
}
.plan-content .plan-content-top-wrapper .plan-content-right {
  width: 100px;
  border: 1px solid #888;
  display: flex;
  flex-direction: column;
}
.plan-content .plan-content-top-wrapper .plan-content-right span {
  height: 30px;
  width: 100%;
  text-align: center;
  line-height: 30px;
  display: inline-block;
  border-bottom: 1px solid #888;
}
.plan-content .plan-content-top-wrapper .plan-content-right .TextArea {
  border-color: transparent;
  padding: 0;
}
.plan-content .content-modal {
  min-height: 200px;
  border: 1px solid #888;
  margin-top: 10px;
  padding: 10px;
}
.plan-content .content-modal .parentName {
  float: right;
  margin-top: -30px;
}
.plan-content .content-modal .parentName .text {
  display: inline-block;
  width: 200px;
  border: 1px solid #8c8a8a;
}
.plan-foot {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.plan-foot span {
  color: #1da57a;
}
.ant-input-disabled {
  color: #000 !important;
}
