.ant-layout {
  background: transparent;
}
.ant-layout .ant-layout-content {
  overflow: auto;
}
.ant-layout .ant-layout-footer {
  display: none;
}
.teacher-manager {
  display: flex;
}
.teacher-manager-card {
  flex: 1 1;
  min-width: 310px;
  margin: 10px;
  max-width: 500px;
  height: 220px;
  padding: 0 10px;
  box-shadow: 4px 4px 5px #888888;
}
.teacher-manager-card .card-top {
  height: 180px;
  width: 100%;
  display: flex;
  align-items: center;
}
.teacher-manager-card .card-top-text {
  flex: 1 1;
  text-align: center;
}
.teacher-manager-card .card-top-text .text-title {
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 24px;
  margin-bottom: 15px;
}
.teacher-manager-card .card-top-text .text-p {
  font-size: 16px;
}
.teacher-manager-card .card-bottom {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.teacher-manager-card .card-bottom a {
  flex: 1 1;
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  margin-bottom: 30px;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: #000;
  text-align: center;
}
.paper {
  padding: 0 30px;
}
.paper .paper-card,
.paper .paper-Input {
  display: flex;
}
.paper .paper-card .paper-span,
.paper .paper-Input .paper-span {
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 16px;
  min-width: 80px;
  text-align: right;
  padding-right: 16px;
}
.paper .paper-card {
  width: 230px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}
.paper .paper-Input {
  margin-top: 10px;
}
.paper .paper-Input .input {
  width: calc(100% - 80px);
  height: 200px;
}
.paper .foot-span {
  font-family: 'Arial Negreta', 'Arial Normal', 'Arial', sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 16px;
  min-width: 80px;
  text-align: right;
  padding-right: 16px;
}
.paper .TextArea {
  width: calc(100% - 80px);
  height: 200px;
}
.paper .foot-btn {
  display: block;
  margin: 20px auto auto auto;
}
.showPaper {
  display: flex;
  width: 100%;
  height: 100%;
}
.showPaper-left {
  width: 200px;
  height: 100%;
}
.showPaper-right {
  width: calc(100% - 230px);
  min-width: 900px;
  height: 100%;
}
.showPaper-right .showPaper-tab {
  border-top: 1px solid #888888;
  border-left: 1px solid #888888;
  border-right: 1px solid #888888;
}
.showPaper-right .showPaper-tab .tab-main {
  min-height: 60px;
  display: flex;
  border-bottom: 1px solid #888888;
}
.showPaper-right .showPaper-tab .tab-main-text {
  display: inline-block;
  text-align: center;
  width: 120px;
  min-width: 120px;
  line-height: 80px;
  border-right: 1px solid #888888;
}
.showPaper-right .showPaper-tab .tab-main-span {
  display: inline-block;
  line-height: 60px;
  width: 120px;
  color: #169bd5;
  text-align: center;
}
.showPaper-right .showPaper-tab .tab-main .selected {
  color: #ff9200;
}
.showPaper-content {
  margin: 30px auto 10px auto;
  width: 80%;
  height: calc(100% - 330px);
}
.showPaper-content .content-main {
  position: relative;
  width: 100%;
  min-height: 200px;
  border: 1px solid #888888;
  border-radius: 5px;
  padding-bottom: 40px;
  margin-bottom: 10px;
}
.showPaper-content .content-main .foot {
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  height: 30px;
  text-align: center;
}
.showPaper-content .content-main .foot .showButton {
  width: 117px;
  height: 30px;
  border-radius: 10px;
  background-color: #0099ff;
}
.showPaper-content .content-main .foot .addButton {
  float: right;
  margin: 5px 20px 0 0;
  color: #169bd5;
}
.createPlan {
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;
}
.createPlan-teacher {
  position: absolute;
  top: -53px;
  right: 230px;
  width: 128px;
  line-height: 45px;
  text-align: center;
  background-color: #169bd5;
  border: none;
  border-radius: 15px;
}
.createPlan-left {
  width: 200px;
  height: 100%;
}
.createPlan-right {
  width: calc(100% - 230px);
  min-width: 900px;
  height: 100%;
  position: relative;
}
.createPlan-right .plan-filter {
  height: 120px;
  width: 100%;
  margin-bottom: 30px;
  border-top: 1px solid #888888;
  border-left: 1px solid #888888;
  border-right: 1px solid #888888;
}
.createPlan-right .plan-filter .tab-main {
  height: 60px;
  display: flex;
  border-bottom: 1px solid #888888;
}
.createPlan-right .plan-filter .tab-main-text {
  display: inline-block;
  text-align: center;
  width: 120px;
  line-height: 80px;
  border-right: 1px solid #888888;
}
.createPlan-right .plan-filter .tab-main-span {
  display: inline-block;
  line-height: 60px;
  width: 120px;
  color: #169bd5;
  text-align: center;
}
.createPlan-right .plan-filter .tab-main .selected {
  color: #ff9200;
}
.createPlan-right .plan-main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.createPlan-right .plan-main .plan {
  width: 800px;
  overflow: hidden;
}
